<template>
  <div class="staking-operations">
    <div class="operations-cover">
      <p class="title">{{ $localizeFilter('operations_history') }}</p>
    </div>
    <StakingOperationsTable :showTable="operations.length > 0" :operations="operations" :showOptions="false" />
    <EmptyTable v-if="operations.length == 0" :text="$localizeFilter('no_operations_staking')" />
    <pagination v-model="page" :records="count" :per-page="pageSize" @paginate="pageChangeHandler" />
  </div>
</template>
  
<script>
import { defineComponent } from '@vue/composition-api'
import StakingOperationsTable from '@/components/StakingOperationsTable'
import paginationMixin from '@/mixins/pagination.mixin'
import localizeFilter from "@/filters/localize.filter"
import EmptyTable from "@/components/EmptyTable"

export default defineComponent({
  props: ['type', 'showOptions', 'coin'],
  components: {
    StakingOperationsTable, EmptyTable
  },
  mixins: [paginationMixin],
  watch: {
    page() {
      this.$nextTick(() => { this.loadData() });
    },
    coin(newVal, oldVal) {
      this.$nextTick(() => { this.loadData() });
    }
  },
  setup() {

  },
  data: () => ({
    operations: [],
    count: 0,
  }),
  mounted() {
    // this.loadData()
  },
  methods: {
    // changeStatus(status) {
    //   this.status = status
    //   this.loadData()
    // },
    reloadData() {
      // this.loadData()
    },
    async loadData() {
      try {
        // // console.log(this.coin)
        const data = { skip: this.skip, limit: this.limit, currencyCode: 'CWPT' }
        this.operations = await this.$store.dispatch('getStakingOperations', data)
        this.operations.forEach(item => {
          item.coin = this.coin;
        })
        const { count } = await this.$store.dispatch('getStakingOperationsCount', data)
        this.count = count
        this.setupPagination(this.operations)
      } catch (e) {
        this.$error(e)
      }
    }
  }
})
</script>
  
