import utils from '@/utils/utils'
import localizeFilter from '../filters/localize.filter';

const sound = require("@/assets/audio/income_sound.mp3");

export default defineComponent({
	props: {
		profitability: {
			type: Number,
			required: true
		},
		title: {
			type: String,
			required: true
		},
		amount: {
			type: Number,
			required: true
		},
	},
	watch: {
	},
	setup() {
	},
	data: () => ({
		unit: 'CWPT'
	}),
	methods: {
	},
	computed: {
		currentlyProfitable() {
			if (this.profitability <= 0.5) {
				return 'burn'
			} else {
				return 'stake'
			}
		},
		formattedAmount() {
			return utils.formatNumber(this.amount, 'en-US', 6, 6).split('.')
		},
		amountImage() {
			return require('@/assets/newImages/coins/CapitalWhalePresaleToken.png')
		},
	},
	mounted() {

	},
})
