<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal">
        <div class="new-modal staking-deposit-modal" @click.stop="">
            <div class="new-modal-back-button-container">
                <button class="new-modal-back-button thin" @click="closeModal"></button>
            </div>
            <div class="modal-content">
        <p class="window-title">{{ titleOverride || $localizeFilter('depositing_funds_into_staking') }}</p>

        <p class="window-text">{{$localizeFilter('deposit_balance_modal_text_amount', ['CWPT'])}}</p>
        <div class="input-field">
            <input id="amount" class="input-class" :placeholder="$localizeFilter('amount_in', ['CWPT'])" type="text" v-model.trim="amount"  :class="{ invalid: v$.amount.$error }"/>
            <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                {{ error.$message }}
            </small>

            <div class="hint">
                {{ $localizeFilter('available_for_deposit_balance', [$utils.formatNumber(balance, 'en-US', 6, 6) + ' CWPT']) }}
            </div>
            
        </div>
        

        <p class="window-text">{{$localizeFilter('specify_the_staking_period_in_weeks')}}</p>
        <div class="input-field">
            <input id="period" class="input-class" :placeholder="$localizeFilter('enter_period_in_weeks')" type="text" v-model.trim="period"  :class="{ invalid: v$.period.$error }"/>
            <small class="error" v-for="(error, index) of v$.period.$errors" :key="index">
                {{ error.$message }}
            </small>

            <div class="hint" v-if="percent">
                {{ $localizeFilter('your_income_will_be_month', [percent + '%']) }}
            </div>
        </div>
        
    </div>
    <button class="basic-button submit-button" :class="submitInProgress ? 'inactive' : ''" @click="submitHandler" >{{$localizeFilter('deposit_funds')}}</button>

    </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { helpers, required, numeric, minValue, maxValue, minLength, maxLength } from "@vuelidate/validators";
import localizeFilter from '@/filters/localize.filter'

function multiplyNumberByDecimals(value, decimalsNumber) {
    // const decimals = decimalsNumber.toString().length
    let [integer, fraction] = value.toString().split('.')
    // console.log(decimalsNumber)
    let res = 0n;
    if(fraction) {
        res += BigInt(fraction) * decimalsNumber;
        for( let i = 0; i < fraction.length; i++ ) {
            res /= BigInt(10);
        }
    }
    res += BigInt(integer) * decimalsNumber;
    return res;
}

export default defineComponent({
    props: {
        source: { // wallet, bonusAccount
            type: String,
            required: true
        },
        titleOverride: {
            type: String,
            default: null
        },
        balance: {
            type: Number,
            required: true
        },
    },
    validations() {
        return {
            amount: {
                // required: helpers.withMessage(({ 
                //     $pending,
                //     $invalid,
                //     $params,
                //     $model
                // })=>`${$pending} ${$invalid} ${$params} ${$model}`, required),
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
                numeric: helpers.withMessage(localizeFilter("field_must_be_numeric"), required),
                minValue: helpers.withMessage(localizeFilter('min_value', [this.minAmount]), minValue(this.minAmount))
            },
            period: {
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
                numeric: helpers.withMessage(localizeFilter("field_must_be_numeric"), required),
                minValue: helpers.withMessage(localizeFilter('min_value', [this.minPeriod]), minValue(this.minPeriod)),
                maxValue: helpers.withMessage(localizeFilter('max_value', [this.maxPeriod]), maxValue(this.maxPeriod))
            }
        };
    },
    setup() {
        return { v$: useVuelidate() };
    },
    data: () => ({
        amount: "",
        minAmount: 0,
        period: null,
        minPeriod: 2,
        maxPeriod: 104,
        currencyCode: 'CWPT',
        submitInProgress: false,
    }),
    methods: {
        closeModal(reload) {
            this.$emit("closeModal", reload);
        },
        async submitHandler() {
            this.v$.$touch();
            if (this.v$.$error /*|| !this.conditionsAccepted*/) {
                return;
            }

            this.submitInProgress = true
            
            // send data to store
            try {
             //   let amount = multiplyNumberByDecimals(this.amount, this.assetDecimalsMap[this.pool.leftCoinTicker]).toString();
                const data = {amount: BigInt(this.amount) * BigInt(1e18), source: this.source, period: Number(this.period), currencyCode: this.currencyCode}
                await this.$store.dispatch('depositStakingBalance', data)
                this.$message(localizeFilter('depositing_funds_to_balance_successful'))
                this.$emit('closeModal')
                setTimeout(()=>{
                    this.submitInProgress = false
                },1000)
            } catch(e) {
                this.$error(e)
                setTimeout(()=>{
                    this.submitInProgress = false
                },1000)
            } 
        },
    },
    computed: {
        percent() {
            if (this.period >=1 && this.period <= 4) {
                return 2
            }
            if (this.period >=5 && this.period <= 8) {
                return 2.1
            }
            if (this.period >=9 && this.period <= 12) {
                return 2.25
            }
            if (this.period >=13 && this.period <= 16) {
                return 2.35
            }
            if (this.period >=17 && this.period <= 20) {
                return 2.65
            }
            if (this.period >=21 && this.period <= 24) {
                return 2.8
            }
            if (this.period >=25 && this.period <= 28) {
                return 3
            }
            if (this.period >=29 && this.period <= 32) {
                return 3.3
            }
            if (this.period >=33 && this.period <= 36) {
                return 3.6
            }
            if (this.period >=37 && this.period <= 40) {
                return 3.9
            }
            if (this.period >=41 && this.period <= 44) {
                return 5
            }
            if (this.period >=45 && this.period <= 48) {
                return 5.25
            }
            if (this.period >=49 && this.period <= 52) {
                return 5.5
            }
            if (this.period >=53 && this.period <= 56) {
                return 5.8
            }
            if (this.period >=57 && this.period <= 60) {
                return 6
            }
            if (this.period >=61 && this.period <= 64) {
                return 6.18
            }
            if (this.period >=65 && this.period <= 68) {
                return 6.4
            }
            if (this.period >=69 && this.period <= 72) {
                return 6.8
            }
            if (this.period >=73 && this.period <= 76) {
                return 7
            }
            if (this.period >=77 && this.period <= 80) {
                return 7.5
            }
            if (this.period >=81 && this.period <= 84) {
                return 8
            }
            if (this.period >=85 && this.period <= 88) {
                return 8.25
            }
            if (this.period >=89 && this.period <= 92) {
                return 8.5
            }
            if (this.period >=93 && this.period <= 96) {
                return 8.75
            }
            if (this.period >=97 && this.period <= 100) {
                return 9
            }
            if (this.period >=101 && this.period <= 104) {
                return 10
            }
            return null
        }
    }
})
</script>


